<template>
  <el-menu class="navbar" mode="horizontal">
    <hamburger class="hamburger-container" :toggleClick="toggleSideBar" :isActive="sidebar.opened"></hamburger>
    <breadcrumb class="breadcrumb-container"></breadcrumb>
    <div class="right-menu">
      <el-dropdown class="avatar-container" trigger="click">
        <el-tooltip effect="dark" content="点击查看更多功能" placement="bottom">
          <div class="avatar-wrapper">
            <!--<el-badge :value="notice" :max="99" class="item" style="height: 40px;">-->
              <img class="user-avatar" :src="avatar">
            <!--</el-badge>-->
            <!--<i class="el-icon-caret-bottom"></i>-->
          </div>
        </el-tooltip>
        <el-dropdown-menu class="user-dropdown" slot="dropdown">
          <router-link class="inlineBlock" to="/">
            <el-dropdown-item>
              主页
            </el-dropdown-item>
          </router-link>
          <el-dropdown-item divided>
            <span @click="changepass=true" style="display:block;">修改密码</span>
          </el-dropdown-item>
          <el-dropdown-item divided>
            <span @click="logout" style="display:block;">退出登陆</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>


    <el-dialog @close="changepass = false;btnLoading = false;" title="输入密码" width="800px" :visible.sync="changepass">
      <el-form :model="user" label-width="80px" >
        <el-row :gutter="20">
          <el-col :span="12" >
            <el-form-item>
              <span slot="label" class="text_css">新密码</span>
              <el-input class="filter-item"  placeholder="密码"  v-model="user.newpassword1"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-button type="primary"  @click="handlepass">保存</el-button>
        </el-row>
      </el-form>
    </el-dialog>


  </el-menu>

</template>

<script>
import { mapGetters } from 'vuex'
import { editInfo } from '@/api/upms/user'
import Breadcrumb from '@/components/Breadcrumb'
import Hamburger from '@/components/Hamburger'
import Screenfull from '@/components/Screenfull'

export default {
  components: {
    Breadcrumb,
    Hamburger,
    Screenfull
  },
  computed: {
    ...mapGetters([
      'name',
      'sidebar',
      'notice',
      'avatar'
    ])
  },
  data() {
    return {
      changepass: false,
      user: {}
    }
  },
  methods: {
    toggleSideBar() {
      this.$store.dispatch('ToggleSideBar')
    },
    logout() {
      this.$store.dispatch('LogOut').then(() => {
        location.reload() // 为了重新实例化vue-router对象 避免bug
      })
    },
    handlepass() {
      console.log(this.$data)
      editInfo(this.$data.user).then((res) => {
        console.log(res)
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.navbar {
  height: 50px;
  line-height: 50px;
  border-radius: 0px !important;
  .hamburger-container {
    line-height: 58px;
    height: 50px;
    float: left;
    padding: 0 10px;
  }
  .breadcrumb-container{
    float: left;
  }
  .screenfull {
    position: absolute;
    right: 90px;
    top: 16px;
    color: red;
  }
  .avatar-container {
    height: 50px;
    display: inline-block;
    position: absolute;
    right: 35px;
    .avatar-wrapper {
      cursor: pointer;
      margin-top: 5px;
      position: relative;
      .user-avatar {
        width: 35px;
        height: 35px;
        border-radius: 50%;
      }
      .el-icon-caret-bottom {
        position: absolute;
        right: -20px;
        top: 25px;
        font-size: 12px;
      }
    }
  }
  .right-menu {
    float: right;
    height: 100%;
    &:focus{
      outline: none;
    }
    .right-menu-item {
      display: inline-block;
      margin: 0 8px;
    }
    .screenfull {
      height: 20px;
    }
  }
}
</style>




